<template>
  <div class="descripcion-content">
    <div class="seccion-cabecera">
      <div class="container">
        <div class="row">
          <div class="col-xs-12">
            <h1><img width="32" :src="require(`../../assets/consulta_azul.png`)" class="mr-12"/>Consulta de procedimientos</h1>
          </div>
        </div>
      </div>
    </div>
    
    <div class="seccion-body">
      <div class="container">
        <div class="row card card-muni">

          <div class="content-block">
          <div class="block-search  mb-12">
            <div class="col-12  col-md-3 ">
          
                  <h5>Realizar Busqueda : </h5>
              
            </div>
            <div class="col-12  col-md-9 ">
              <div class="row">
                <div class="example ex1 col-6  col-lg-3">
                  <label class="radio blue ">
                    <input
                    type="radio"
                    name="optradio"
                    checked
                    v-model="tipoFiltro"
                    value="1"
                    @change="CambiarTipoFiltro()"
                  />
                    <span>Por Unidad Orgánica</span>
                  </label>
                </div>
                <div class="example ex1 col-6  col-lg-3">
                  <label class="radio blue">
                    <input
                    type="radio"
                    name="optradio"
                    v-model="tipoFiltro"
                    value="2"
                    @change="CambiarTipoFiltro()"
                  />
                    <span>Por Procedimiento</span>
                  </label>
                </div>

              </div>
              
            </div>

          </div>


          <div class="block-search    mb-12" v-if="mapListaArea && tipoFiltro=='1'">
            <div class="inline-option col-12 col-sm-3">
              <h5>Unidad Orgánica : </h5>
            </div>
            <div class="inline-option col-12 col-sm-9">
                <div class="search-div">
                  <model-select
                    :options="mapListaArea"
                    v-model="itemArea"
                    placeholder="Seleccione un Unidad Orgánica"
                    @input="ObtenerTipoTramite"
                  ></model-select>
              </div>
            </div>
          </div>

          <div class="block-search    mb-12" v-if="mapListaTipoTramite">
            <div class="inline-option col-12 col-sm-3">
              <h5>Procedimiento : </h5>
            </div>
            <div class="inline-option  col-12 col-sm-6">
                <div class="search-div">
                  <model-select
                    :options="mapListaTipoTramite"
                    v-model="itemTramite"
                    placeholder="Seleccione un Servicio"
                    @input="CambioTramite(itemTramite)"
                  ></model-select>
                </div>
            </div>
            <div v-if="!listaAreaConsulta" class="inline-option col-12 col-sm-3">
              <button @click="BuscarTramite()" class="btn btn-buscar-tramite">Buscar Trámite</button>
            </div>
          </div>


          <div class="block-search" v-if="listaAreaConsulta">
            <div class="inline-option col-12 col-sm-3">
              <h5>Unidad Orgánica : </h5>
            </div>
            <!-- demo -->
            <div class="inline-option  col-12 col-sm-6">
                <div class="search-div">
                  <model-select
                    :options="mapListaAreaConsulta"
                    v-model="itemAreaConsulta"
                    placeholder="Seleccione un Unidad Orgánica"
                    @input="ObtenerTipoAreaConsulta(mapListaAreaConsulta,itemAreaConsulta)"
                  ></model-select>
              </div>
            </div>
            <!-- demo -->
           <!-- -->
            <!-- <div class="inline-option fl-12">
              <div class="col-md-12">
                <div class="search-div">
                  <div class="example ex1">
                <label class="radio orange" v-for="area of listaAreaConsulta" :key="area.area.uniorgcod">
                  <input
                            type="radio"
                            name="optradio2"
                            v-model="itemTramiteConsulta"
                            :value="area.tipoTramite.idTipoTramite"
                          />
                  <span>{{area.area.uniorgnom}}</span>
                </label>
              </div>
                </div>
              </div>
            </div> -->
            <!-- --> 
            <div v-if="listaAreaConsulta" class="inline-option  col-12 col-sm-3">
              <button @click="BuscarTramite()" class="btn btn-buscar-tramite">Buscar Trámite</button>
            </div>
          </div>

          </div>
        </div>
      </div>
    </div>
    <div v-if="mostrarPantalla">
      <div class="seccion-body">

        <div class="container">      
          <div class="seccion-subtitulo  mb-24">
            <p>
              {{descripcionTipoTramite}}
            </p>
          </div>

            <ul class="lista-principal">
              <li>
                <p class="principal">
                  <span>&#x2714;</span>
                  <b> Costo</b>
                </p>
                <span class="principal-detalle  pl-32">{{precioTramite==0 ? 'El trámite es gratuito' : 'El trámite tiene un costo de S/.' + precioTramite}}</span>
              </li>
              <li >
                <p class="principal">
                  <span>&#x2714;</span>
                  <b>{{id014Tramite==33 ? 'Información TUSNE' : id014Tramite==34 ? 'Información TUPA' : 'Requisitos'}}</b>
                </p>
                <span class="principal-detalle">
                  <div
                    v-if="(codigoTipoTramite!=null && codigoTipoTramite!=''  )"
                    v-html="tupaTusneDescripcion"
                  ></div>
                  <div v-else>
                    <ol class="lista-ol  pl-16">
                      <li
                        v-for="requisito of listaRequisitos"
                        :key="requisito.idRequisitoTramite"
                      >{{requisito.requisito.nombre}}</li>
                    </ol>
                  </div>
                </span>
              </li>
              <li v-if="baseLegal != null && baseLegal.length > 7">
                <p class="principal">
                  <span>&#x2714;</span>
                  <b>Base Legal</b>
                </p>
                <span class="principal-detalle    pl-32">
                  <div v-html="baseLegal"></div>
                </span>
              </li>
              <li v-if="tenerEnCuenta != null && tenerEnCuenta.length > 7">
                <p class="principal">
                  <span>&#x2714;</span>
                  <b>Tener en Cuenta</b>
                </p>
                <span class="principal-detalle    pl-32">
                  <div v-html="tenerEnCuenta"></div>
                </span>
              </li>
              <li v-if="preguntasFrecuentes != null &&  preguntasFrecuentes.length > 7">
                <p class="principal">
                  <span>&#x2714;</span>
                  <b>Preguntas Frecuentes</b>
                </p>
                <span class="principal-detalle    pl-32">
                  <div v-html="preguntasFrecuentes"></div>
                </span>
              </li>
            </ul>
            <br>
        </div>
      </div>


      <div class="seccion-body  mb-24">
        <div class="container">
          <div class="row">
            <div class="col-12">
              <div class="como-registrar">
                <p>¿Cómo registrar tu trámite?</p>
            </div>
            </div>
          </div>
        </div>
        <div class="container">
          <div class="row">
            <div class="col-12">
              <div v-if="flagOnLine == true">
                <div class="seccion-subtitulo">
                  <p>Este procedimiento puede ser registrado presencialmente o de manera
                online. Para presentarlo de manera online deberá seguir los siguientes
                pasos:</p>
                </div>
                
                <div class="registro-online row">

                  <div class="registro-online__item  col-12 col-sm-6  col-lg-3">
                    <div class="registro-step">
                      <div class="flecha  d-none  d-lg-block">
                        <img :src="require(`../../assets/flecha_derecha.png`)"/>
                      </div>
                      <p class="step">Ingresa a la</p>
                      <div class="content-img-xl">
                        <a class="enlace-plataforma-digital" target="_blank" href="https://www.miraflores.gob.pe/plataforma-digital/">
                          <img width="100%" src="https://www.miraflores.gob.pe/wp-content/themes/munimiraflores/web-imagenes/logo-plataforma-virtual.png" alt=""> 
                        </a>                         
                      </div>
                      <p class="titulo">
                      </p>
                      <p class="descripcion">
                        <a class="enlace-plataforma-digital" target="_blank" href="https://www.miraflores.gob.pe/plataforma-digital/">
                         https://www.miraflores.gob.pe/plataforma-digital/</a> 
                      </p>
                    </div>
                  </div>

                  <div class="registro-online__item  col-12 col-sm-6  col-lg-3">
                    <div class="registro-step">
                      <div class="flecha  d-none  d-lg-block">
                        <img :src="require(`../../assets/flecha_derecha.png`)"/>
                      </div>
                      <p class="step"> Selecciona la opción ingresa tu cuenta </p>
                      <div class="content-img">
                        <img :src="require(`../../assets/login.png`)"/>
                      </div> 
                      <p class="titulo">
                      </p>
                      <p class="descripcion">Digita tu correo y contraseña e ingresar.</p>
                    </div>
                  </div>
                  <!--mesa -->
                  <div class="registro-online__item  col-12 col-sm-6  col-lg-3">
                    <div class="registro-step">
                      <div class="flecha  d-none  d-lg-block">
                        <img :src="require(`../../assets/flecha_derecha.png`)"/>
                      </div>
                      <p class="step">
                        Seleccionar la opción “Mesa de Partes Digital”
                      </p>
                      <div class="content-img">
                        <img :src="require(`../../assets/mesadepartes.png`)"/>
                      </div>                      
                      <p class="titulo"> Mesa de Partes Digital</p>
                      <p class="descripcion">Registra tus trámites de manera virtual.</p>         
                    </div>
                  </div>
                  <!--3 elemntos -->
                  <div class="registro-online__item  col-12 col-sm-6  col-lg-3">
                    <div class="registro-step">
                      <p class="step">
                        Seleccionar la opción “{{this.tipoDocumento[this.campoSelecionado].nombre}}”
                      </p>
                      <div class="content-img">
                        <img :src="require(`../../assets/${this.tipoDocumento[this.campoSelecionado].src}.png`)"/>
                      </div>                      
                      <p class="titulo"> {{this.tipoDocumento[this.campoSelecionado].nombre}}</p>
                      <p class="descripcion">{{this.tipoDocumento[this.campoSelecionado].descripcion}}</p>         
                    </div>
                  </div>
                </div>

              </div>
              <div v-else-if="flagOnLine == false">
                <b>Este procedimiento sólo puede ser registrado presencialmente.</b>
              </div>
              <div v-else>
                ---
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
          
    <div class="search-not" v-else>
      <p>Aquí se mostrará el resultado de busqueda</p>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import EventBus from "../../helpers/EventBus.js";
import Constantes from "../../store/constantes.js";
import { ModelSelect } from "vue-search-select";
import _ from "lodash";
export default {
  components: {
    ModelSelect,
  },
  created() {
    this.tipoDocumento = [
      {
        nombre: 'Nuevo Trámite',
        src: 'nuevotramite',
        descripcion: 'Ingresar nuevo trámite TUPA o TUSNE'
      },
      {
        nombre: 'Anexar Documento',
        src: 'anexar',
        descripcion: 'Ingresar documento a un nuevo expediente existente'
      },
      {
        nombre: 'Presentar Carta',
        src: 'carta',
        descripcion: 'Ingresar nuevo documento no incluido en el TUPA o TUSNE'
      }
    ]
    this.ObtenerArea();
    /* this.idArea=this.$route.params.idArea;
    this.tipoDoc = this.$route.query.new;
    this.cargarTipoTramite();
    this.cargarUnidadOrganica();
    this.cargarRequisitos(); */
  },
  data() {
    return {
      itemAreaConsulta:'',
      mostrarPantalla: false,
      tipoDoc: null,
      nombreTipoTramite: "",
      codigoTipoTramite: "",
      idTipoTramite: 0,
      idTipoTramiteIngreso: 0,
      id009TipoTramite: 0,
      flagOnLine: false,
      idArea: 0,
      nombreArea: "",
      descripcionTipoTramite: "",
      listaRequisitos: null,
      listaCanalAtencion: null,
      listaPreguntas: null,
      secuenciaSamd: 0,
      letraSamd: "",
      idSubConcepto: 0,
      precioTramite: 0,
      nombreUsuario: "",
      tupaTusneDescripcion: "",
      baseLegal: "",
      tenerEnCuenta: "",
      preguntasFrecuentes: "",
      id014Tramite: 0,
      tipoFiltro: "1",
      modoTramite: "0",
      mapListaArea: null,
      mapListaAreaConsulta: null,      
      listaTipoTramite: null,
      mapListaTipoTramite: null,
      listaArea: null,
      itemTramite: 0,
      itemArea: null,
      listaAreaConsulta: null,
      itemTramiteConsulta: null,
      tipoDocumento:[],
      campoSelecionado: '',
    };
  },
  filters: {
    upper: function (value) {
      return value.toLowerCase();
    },
  },
  methods: {
    PaginaVista(){
      dataLayer.push({
          'event': 'pagina_vista',
          'page-url': '/procedimientos', 
          'page-title': 'procedimientos'
      });
    },
    ObtenerTipoAreaConsulta(listaArea,codigoArea){
      this.mostrarPantalla = false
      this.flagOnLine = false
      for (let index = 0; index < listaArea.length; index++) {
        console.log(listaArea[index].value)
        if (codigoArea == listaArea[index].value) {
          this.itemTramite = listaArea[index].id
          console.log("TRUE",this.itemTramite)
        }else{
          console.log("false")
        }
      }
      
    },
    CambioTramite(itemTramite) {
      this.mostrarPantalla = false
      this.flagOnLine = false
      this.listaAreaConsulta = null;
      this.listaTipoTramite.forEach(element => {
              if(element.idTipoTramite==itemTramite && element.conteoConsulta>1){
                this.CargarAreaConsulta(itemTramite);
              }
          });
    },
    CargarAreaConsulta(itemTramite) {
      axios
        .get(
          Constantes.rutaTramite + "tipo-tramite-area-consulta/" + itemTramite
        )
        .then((response) => {
          this.listaAreaConsulta = response.data.data;
          this.ObtenerAreaConsulta(this.listaAreaConsulta)
          
          
        })
        .catch((e) => console.log(e));
    },
    ObtenerAreaConsulta(lista) {
      this.listaArea = null;
      this.mapListaAreaConsulta = lista.map(function(obj){ 
        let objeto = []
        objeto.id = obj.tipoTramite.idTipoTramite 
        objeto.value = obj.area.uniorgcod
        objeto.text = obj.area.uniorgnom
        return objeto
      });
      EventBus.$emit("loadershow", false);
    },
    CambiarModoTramite() {
      this.id009TipoTramite = null;
      this.itemArea = null;
      this.itemTramite = null;
      this.itemTramiteConsulta = null;
      this.listaAreaConsulta = null;
      if (this.tipoFiltro == "2") {
        this.mapListaArea = null;
        this.ObtenerTipoTramite2();
      } else {
        this.mapListaTipoTramite = null;
        this.ObtenerArea();
      }
    },
    CambiarTipoFiltro() {
      this.flagOnLine = false
      this.mostrarPantalla = false
      this.id009TipoTramite = null;
      this.itemArea = null;
      this.itemTramite = null;
      this.itemTramiteConsulta = null;
      this.listaAreaConsulta = null;
      if (this.tipoFiltro == "2") {
        this.mapListaArea = null;
        this.ObtenerTipoTramite2();
      } else {
        this.mapListaTipoTramite = null;
        this.ObtenerArea();
      }
    },
    BuscarTramite() {
      this.mostrarPantalla = false
      this.cargarTipoTramite();
      this.cargarUnidadOrganica();
      this.cargarRequisitos();
    },
    ObtenerTipoTramite2() {
        axios.get(Constantes.rutaTramite+'tipo-tramite-consulta/'+this.modoTramite)
        .then((response) => {
          this.listaTipoTramite = response.data.data;
          var i = 0;
          this.mapListaTipoTramite = [];
          while (i < this.listaTipoTramite.length) {
            var asunto = {};
            asunto.value = this.listaTipoTramite[i].idTipoTramite;
            asunto.text = this.listaTipoTramite[i].nombre;
            asunto.conteoConsulta = this.listaTipoTramite[i].conteoConsulta;
            this.mapListaTipoTramite.push(asunto);
            i++;
          }
        })
        .catch((e) => console.log(e));
    },
    ObtenerArea() {
      this.listaArea = null;
      axios
        .get(Constantes.rutaTramite + "areaxtipo/" + this.modoTramite + "/1")
        .then((response) => {
          this.listaArea = response.data;
          this.mapListaArea = _.map(this.listaArea, function (i) {
            _.pick(i, "idArea", "nombreArea");
            return _.assign(i, {
              value: i.idArea.toString(),
              text: i.nombreArea.toString(),
            });
          });
          EventBus.$emit("loadershow", false);
        })
        .catch((e) => console.log(e));
      // }
    },
    ObtenerTipoTramite() {
      this.mostrarPantalla = false
      this.flagOnLine = false
      // if (!this.$root.$cache) {
      // EventBus.$emit("loadershow", true);
      var endpoint =
        Constantes.rutaTramite +
        "tramite-area-tipo/" +
        this.itemArea +
        "/" +
        this.modoTramite;
      //Constantes.rutaTramite +"tipotramite/-/"+this.itemArea
      axios
        .get(endpoint)
        .then((response) => {
          this.listaTipoTramite = response.data.data;
          let mapListaTipoTramite = _.map(this.listaTipoTramite, function (i) {
            _.pick(i, "idTipoTramite", "nombre");
            return _.assign(i, {
              value: i.idTipoTramite.toString(),
              text: i.nombre.toString(),
            });
          });
          // this.$root.$cache = _.clone(mapListaTipoTramite, true);
          this.mapListaTipoTramite = mapListaTipoTramite;
          // EventBus.$emit("loadershow", false);
        })
        .catch((e) => console.log(e));
      // } else {
      //   this.mapListaTipoTramite = this.$root.$cache;
      // }
    },
    cargarPrecio() {
      this.precioTramite = 0;
      if (this.codigoTipoTramite >= 0 && this.idSubConcepto >= 0 && this.codigoTipoTramite != null && this.idSubConcepto != null && this.codigoTipoTramite != "" && this.idSubConcepto != "") {
        axios
          .get(
            Constantes.rutaTramite +
              "tipotramite/precio/" +
              this.codigoTipoTramite +
              "/" +
              this.idSubConcepto
          )
          .then((response) => {
            this.precioTramite = response.data.precio;
          })
          .catch((e) => console.log(e));
      }else{
        
        this.precioTramite = "0.00"
      }
    },
    cargarRequisitos() {
      EventBus.$emit("loadershow", true);
      axios
        //.get(Constantes.rutaTramite +"requisito-tramite/" +this.$route.params.idTipoTramite + "/" +this.idArea)
        .get(
          Constantes.rutaTramite +
            "requisito-tramite/" +
            this.itemTramite +
            "/" +
            0
        )
        .then((response) => {
          this.listaRequisitos = response.data.data;
          this.mostrarPantalla = true
          EventBus.$emit("loadershow", false);
        })
        .catch((e) => console.log(e));
    },
    IniciarTramite() {
      if (this.listaRequisitos == null || this.listaRequisitos.length == 0) {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Aún no se han definido requisitos para este trámite",
        });
      } else {
        if (this.flagOnLine) {
          let endpoint = this.tipoDoc
            ? "/tramite/ce/" +
              this.idArea +
              "&" +
              this.$route.params.idTipoTramite +
              "&0&0" +
              "?new=3"
            : "/tramite/ce/" +
              this.idArea +
              "&" +
              this.$route.params.idTipoTramite +
              "&0&0";
          this.$router.push(endpoint);
        } else {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Trámite no disponible de manera online.",
          });
        }
      }
    },
    cargarUnidadOrganica() {
      var rutaprod = "https://mz-services.miraflores.gob.pe:8090/api/tramites/";
      axios
        .get(rutaprod + "unidadorganica/" + this.itemAreaConsulta)
        .then((response) => {
          this.nombreArea = response.data.data[0].uniorgnom;
        })
        .catch((e) => console.log(e));
    },
    tipoDocumentoSeleccionado(equivalenciaOracle, codigo){
      if (equivalenciaOracle == 38) {
        this.campoSelecionado = 1
      }else if(equivalenciaOracle == 37 || equivalenciaOracle == 39){
        if (codigo == null || codigo == '') {
          this.campoSelecionado = 2
        }else{
          this.campoSelecionado = 0
        }
      }
    },
    cargarTipoTramite() {
      let idTipoTramite = this.itemTramiteConsulta
        ? this.itemTramiteConsulta
        : this.itemTramite;
      axios
        .get(
          //Constantes.rutaTramite +"tipotramite/inicializar/" +this.itemTramite
          Constantes.rutaTramite + "tipotramite/inicializar/" + idTipoTramite
        )
        .then((response) => {
          this.id009TipoTramite = response.data.data.id009TipoMetodo.idParametro;
          this.codigoTipoTramite = response.data.data.codigo;
          this.nombreTipoTramite = response.data.data.nombre;
          this.flagOnLine = response.data.data.flagOnLine;
          this.idTipoTramiteIngreso = response.data.data.id008EquivalenciaOracle;
          this.idTipoTramite = this.$route.params.idTipoTramite;
          this.descripcionTipoTramite = response.data.data.descripcion;
          this.idSubConcepto = response.data.data.idSubConcepto;
          this.tupaTusneDescripcion = response.data.data.tupaTusneDes;
          this.baseLegal = response.data.data.baseLegal;
          this.tenerEnCuenta = response.data.data.tenerEnCuenta;
          this.preguntasFrecuentes = response.data.data.preguntasFrecuentes;
          this.id014Tramite = response.data.data.id014Tramite;
          this.cargarPrecio();
          this.tipoDocumentoSeleccionado(this.idTipoTramiteIngreso, this.codigoTipoTramite)
          //console.log(this.listaTipoTramite[0].nombre);
        })
        .catch((e) => console.log(e));
    },
  },
  mounted() {
    PaginaVista()
  }
};
</script>
<style lang="scss" scoped>
.cursor-pointer{
  cursor: pointer;
}
.registro-online{
  padding-top: 24px;
  
  p{
    margin: 0;
    padding: 0;
  }
  &__item{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 16px 8px;
    height: 260px;
    position: relative;
    .flecha{
      position: absolute;
      right: 0;
      top: 80px;
      img{
        width: 24px;
        opacity: .7;
      }
    }
    .content-img-xl{
      max-width: 100px;
    }
    .content-img{
      background: #eaf0f9;
      border-radius: 50%;
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      img{
        width: 40px;
      }
    }
    .titulo{
      margin: 12px 0;
      color: #0078cf;
      font-size: 15px;
      font-weight: bold;
    }
    .descripcion{
      color: #515151;
      font-size: 14px;
      max-width: 200px;
      word-break: break-word;
      a{
        color: #0078cf;
        text-decoration: underline;
      }
    }
  }
  .registro-step{
    max-width: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .step{
      height: 50px;
      display: flex;
      align-items: center;
    }
  }
  p{
    text-align: center;
  }
}
.documento{
  padding: 15px;
  width: 194px;
  background: aliceblue;
  border: 1px solid #0078cf;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &--titulo{
    font-weight: bold;
    color: #0078cf;
    font-size: 16px;
    margin-bottom: 20px;
  }
  img{
    max-width: 60px;
    margin-bottom: 15px;
  }
  &--descripcion{
    color: #7d7d7d;
    font-size: 14px;
    text-align: center;
  }
}
.lista-principal{
    .suma{
      color: #8BC34A;
      font-size: 22px;
      font-weight: bold;
    }
    .principal-detalle{
      .lista-ol{
        list-style: none !important;
      }
    }
  }

    

  button{
    width: 100%;
    text-align: left;
    text-decoration: none;
    color: #636363;
    display: flex;
    align-items: center;
    &:focus{
      border: none;
      box-shadow: none;
    }
}
.breadcrumb{
  margin-bottom: 0;
}
.descripcion-content {
  height: 100%;
  position: relative;
  margin-bottom: 100px;
  color: #757575;
  .btn-buscar-tramite{
    margin-top: 24px;
    height: 45px;
    background: #0078cf;
    color: white;
    font-size: 17px;
    justify-content: center;
  }
  .card-muni{
    padding: 0 16px;
    margin-top: 0;
  }
  .seccion-cabecera{
    background: white;
    h1{
      padding: 0 16px;
      height: 100px;
      line-height: 100px;
      color: #0078cf;
      font-size: 24px;
      
    }
  }
  .seccion-body{
    background: white;
    padding: 24px 0;
    margin-top: 24px;
    .seccion-subtitulo{
      font-size: 16px;
      color: #515151;
    }
    .como-registrar{
      p{
        font-size: 24px;
        color: #515151;
      }
    }
    .content-block{
      width: 100%;
    }
  }
  .lista-registro-online{
    border: none ;
    .enlace-plataforma-digital{
      color: #0078cf;
    }
    li{
      border: none;
      background: transparent;
    }
    .mesa-de-partes{
      padding: 15px 22px;
      width: 194px;
      border-radius: 6px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      .content-img{
        background: #dff0ff;
        width: 72px;
        height: 72px;
        border-radius: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 8px;
        img{
          width: 50px;
        }
      }
    }
  }
  .mobile-block {
    display: none;
  }
  .top-title {
    display: flex;
    align-content: space-between;
    div:first-child {
      width: 82%;
    }
    div:last-child {
      width: 18%;
      text-align: right;
    }
  }
  ul {
    margin-left: 0px;
    padding-left: 0px;
    list-style: none;
    border: none;
    overflow: hidden;
    margin-bottom: 0px;
    li {
      border-bottom: none;
      > span {
        padding: 10px;
        display: block;
      }
      .principal{
        margin-bottom: 0px;
        padding: 10px;
        background: #0078cf0d;
        b,span{
          color: #0078cf;
        }
        
      }
      .principal-detalle{
        ol{
          list-style: none;
          padding-left: 0;
        }
      }
    }
  }
  h5 {
    font-size: 16px;
    color: #757575;
    font-weight: normal;
  }
  .area {
    color: #2db5c3;
    font-weight: 500;
    text-transform: capitalize;
  }
}
.search-not {
  border: solid 1px #eaf0f9;
    margin-bottom: 30px;
    text-align: center;
    padding-top: 18px;
    color: #bbbbbb;
}
.block-search {
  display: flex;
  border-bottom: 0;
  flex-direction: column;
  align-items: flex-start;
  .inline-option {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-right: solid #eaf0f9 1px;
    h5 {
      margin-bottom: 0;
    }
  }
  & .inline-option:last-child {
    border-right: 0;
  }
}
.search-div {
  width: 100%;
  .radio {
    margin-bottom: 10px;
  }
}
/* radio button */
.example {
  display: inline-block;
  text-align: left;
}
.example input {
  display: none;
}
.example label {
  margin-bottom: 0;
  display: inline-block;
  cursor: pointer;
  width: 170px;
}

.ex1 span {
  display: block;
  padding: 10px 10px 10px 25px;
  border: 2px solid #ddd;
  border-radius: 20px;
  position: relative;
  transition: all 0.25s linear;
}
.ex1 span:before {
  content: "";
  position: absolute;
  left: 5px;
  top: 50%;
  -webkit-transform: translatey(-50%);
  transform: translatey(-50%);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #ddd;
  transition: all 0.25s linear;
}
.ex1 input:checked + span {
  background-color: #fff;
  text-align: left;
  // box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
}
.ex1 .blue input:checked + span {
  color: #0078cf !important;
  border-color: #0078cf !important;
}
.ex1 .blue input:checked + span:before {
  background-color: #0078cf !important;
}
.ex1 .orange input:checked + span {
  color: orange;
  border-color: orange;
}
.ex1 .orange input:checked + span:before {
  background-color: orange;
}
.fl-1 {
  min-width: 16% !important;
}
.fl-11 {
  min-width: 42% !important;
}
.fl-12 {
  min-width: 65% !important;
}
.fl-13 {
  min-width: 19% !important;
}
.fl-4 {
   min-width: 84% !important;
}
@media only screen and (max-width: 600px) {
  .descripcion-content {
    ul {
      margin-bottom: 0px;
    }
    .mobile-block {
      padding-top: 40px;
      padding-bottom: 60px;
      text-align: center;
      display: block;
    }
    .top-title {
      flex-direction: column-reverse;
      > div {
        width: 100% !important;
      }
      h5 {
        width: 100%;
      }
    }
    .mobile-hide {
      display: none;
    }
  }
}

@media  (min-width: 1024px) {
  .descripcion-content {
    .btn-buscar-tramite{
      margin-top: 0px;
    }
  }
  .block-search {
  flex-direction: row;
  align-items: center;
  }
  .descripcion-content{
    .seccion-cabecera{
      h1{
        padding: 0 16px;
        height: 100px;
        line-height: 100px;
        color: #0078cf;
        font-size: 34px;
        
      }
    }
  }
  .example label{
    cursor: pointer;
    width: 180px;
  }
}
</style>


